<template>
    <div id='body'>
        <div class='text'>
            {{ text }}
        </div>
        <div>
            <img src='../../../static/img/哭，404.png' alt='' />
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            text: '抱歉，您访问的页面已失联...'
        }
    }
}
</script>
<style scoped>
    #body{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .text{
        font-size:30px;
        font-weight: bold;
        color:white;
    }
</style>




