<template>
    <div class="editor">
        <Input class='title' v-model="title" placeholder="请输入标题" />
        <Select class='category' v-model="category" placeholder="请选择分类" transfer>
            <Option v-for="item in cateList" :value="item.name" :key="item._id">{{ item.name }}</Option>
        </Select>
        <editor ref='editor'></editor>
        <Button type='info' class='btn' @click="Save">保存</Button>
        <Button type='info' class='btn' @click="clear">清空</Button>
    </div>
</template>
<script type="text/ecmascript-6">
    import { reqTags,reqNoteContent } from '../../api'
    import editor from '../../components/Editor/wangEdit.vue' // 根据editor.vue组件位置引入
    export default {
        components: {
            editor
        },
        data(){
            return{
                id: '',//已有笔记对应的id
                title: '', //标题
                category: '', //选择的分类
                cateList: [], //分类数组
            }
        },
        methods: {
            //获取已有分类
            async getCategory(){
                const result = await reqTags()
                if(result.code === 1){
                    this.cateList = this.cateList.concat(result.data)
                }else{
                    this.$Message.error(result.msg)
                }
            },
            //获取原有内容
            async getContent(){
                this.id = this.$route.query.id
                if(!this.id) return
                const result = await reqNoteContent(this.id)
                if(result.code === 1){
                    this.title = result.data.title
                    this.category = result.data.category
                    this.$refs.editor.editor.txt.html(result.data.content)
                }else{
                    console.log(result.msg)
                }
            },
            //保存内容
            Save(){
                if(this.title === ''){
                    this.$Message.warning('请输入标题')
                    return
                }
                if(this.category === ''){
                    this.$Message.warning('请选择分类')
                    return
                }
                let html = this.$refs.editor.editor.txt.html()
                let text = this.$refs.editor.editor.txt.text()
                this.$http.post('/api/saveNote',{
                    id: this.id,
                    html: html,
                    text: text,
                    title: this.title,
                    category: this.category
                })
                .then(res=>{
                    if(res.data.code === 1){
                        this.$Message.success(res.data.msg)
                    }else{
                        this.$Message.error(res.data.msg)
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
            },
            // 清空内容
            clear () {
                this.$refs.editor.editor.txt.clear()
            }
        },
        created(){
            this.getContent()
            this.getCategory()
        }
    }
</script>
<style scoped >
    .editor{
        width: 90%;
        margin: auto;
    }
    .title{
        width: 100%;
        margin: 0 0 10px 0;
    }
    .category{
        width: 40%;
        margin: 0 0 10px 0;
    }
    .btn{
        margin: 10px 10px 0 0;

    }
</style>


