/*
包含n个接口请求函数的模块
函数的返回值: promise对象
*/
import ajax from './ajax'
const BASE_URL = '/api'

//1.获取音乐列表
export const reqMusic = () => ajax(BASE_URL + '/getMusic')
//2.获取相册信息
export const reqAlbums = () => ajax(BASE_URL + '/getAlbum')
//3.根据相册id,group获取照片信息
export const reqPhotos = (album_id,group) => ajax(BASE_URL + '/getPhoto', {album_id,group})
//4.获取日记信息
export const reqDiarys = () => ajax(BASE_URL + '/getDiary')
//5.根据id获取日记内容
export const reqDiaryContent = (id) => ajax(BASE_URL + '/getDiaryContent',{id})
//6.获取笔记信息
export const reqNotes = () => ajax(BASE_URL + '/getNote')
//7.根据id获取笔记详情
export const reqNoteContent = (id) => ajax(BASE_URL + '/getNoteContent',{id})
//8.获取标签信息
export const reqTags = () => ajax(BASE_URL + '/getTag')
//9.获取个人信息(头像、昵称)
export const reqUserInfo = () => ajax(BASE_URL + '/getUserInfo')
//10.获取首页轮播图片
export const reqCarouselImgs = () => ajax(BASE_URL + '/getCarouselImg')



