<!--头部组件-->

<template>
    <div class='header'>
        <!-- <img src='../../../static/img/1.jpg' alt='' class='headimg'/>
        <div class='menu'>
            <ul>
                <li v-for="(item,index) in menus" :key="index">
                    <router-link class='item' :to='item.url'>{{ item.title }}</router-link>
                </li>
            </ul>
        </div> -->
        <div class='avatar'>
            <Upload 
                ref="upload"
                :show-upload-list="false"
                :on-success="handleSuccess"
                :on-error="handleError"
                :format="['jpg','jpeg','png']"
                :max-size="2048"
                :on-format-error="handleFormatError"
                :on-exceeded-size="handleMaxSize"
                :before-upload="handleBeforeUpload"
                multiple
                action="/api/uploadAvatar">
                <Button type='info' class='btn-avatar'>更换头像</Button>
            </Upload>
            <img :src='avatar' alt='' class='img-avatar'/>
        </div>
        <div class='right'>
            <div id='name'>
                {{ name }}
            </div>
            <Menu mode="horizontal" :active-name="activeName" class='menu'>
                <MenuItem v-for='item in menus' :name="item.id" :key='item.id'>
                    <router-link :to='item.url' class='item'>
                        <Icon :type="item.icon" />
                        {{ item.title }}
                    </router-link>
                </MenuItem>
            </Menu>
        </div>
    </div>
</template>
<script>
import { reqUserInfo } from '../../api'
export default {
    data(){
        return{
            name: '', //昵称
            activeName: 1, //当前选中的菜单项
            menus: [
                {
                    id : 1,
                    title: '主页',
                    url: '/home',
                    icon: 'ios-home',
                },
                {
                    id : 2,
                    title: '往事记忆',
                    url: '/album',
                    icon: 'ios-camera',
                },
                {
                    id : 3,
                    title: '记录点滴',
                    url: '/diarys',
                    icon: 'ios-book',
                },
                {
                    id : 4,
                    title: '随笔心得',
                    url: '/notes',
                    icon: 'ios-bookmarks',
                },
            ],
            avatar : '',//头像
        }
    },
    methods:{
        //上传成功
        handleSuccess (res, file) {
            //在前端显示
            if(res.code === 1){
                this.avatar = './static/img/avatar/' + res.url
                this.$Message.success('头像上传成功')
            }else{
                this.$Message.error(res.msg)
            }
        },
        //上传失败
        handleError (err, file){
            console.log(err)
            this.$Message.error("头像上传失败")
        },
        //上传的格式错误
        handleFormatError (file) {
            this.$Notice.warning({
                title: '上传文件的格式错误',
                desc: '图片格式 ' + file.name + ' 错误, 请选择.jpg，.jpeg，.png的文件'
            });
        },
        //超过可上传文件大小
        handleMaxSize (file) {
            this.$Notice.warning({
                title: '超过可上传文件大小限制',
                desc: '上传图片  ' + file.name + ' 不能超过2M'
            });
        },
        //上传处理
        handleBeforeUpload (file) {
            event.preventDefault() //阻止冒泡事件
        },
        //异步获取用户信息(昵称、头像)
        async getUserInfo(){
            const result = await reqUserInfo()
            //数据读取成功
            if(result.code === 1) {
                this.name = result.data.username;
                this.avatar = './static/img/avatar/' + result.data.avatar;
            }else{
                this.$Message.error(result.msg)
            }
        },
        //根据路径获取选中项
        getActiveNameByPath(){
            let pathname = window.location.pathname;
            if(pathname.includes('home')){
                this.activeName = 1;
            }else if(pathname.includes('album')){
                this.activeName = 2;
            }else if(pathname.includes('diarys')){
                this.activeName = 3;
            }else if(pathname.includes('notes')){
                this.activeName = 4;
            }
        }
    },
    mounted(){
        this.getActiveNameByPath();
        this.getUserInfo();
    }
}
</script>
<style scoped>
    /* *{
        margin: 0;
        padding: 0;
    }
    .headimg{
        width: 100%;
        height: 100%;
        vertical-align: bottom;
    }
    .menu{
        padding: 10px 0px;
        white-space: nowrap;
        width: 100%;
        font-family: "微软雅黑";
        font-size: 16px;
        background:linear-gradient(rgba(228, 144, 18, 0.7), rgba(228, 144, 18, 0.5));
        text-align: center;
        position: relative;
    }
    .menu ul {
        width: 60%;
        margin:auto;
        display: flex;
        justify-content: space-around;
    }
    .menu ul li {
        list-style: none;
    }
    .menu .item{
        padding: 10px 20px;
        text-decoration: none;
        color:blanchedalmond;
    }
    .menu .item:hover{
        background: rgba(252, 123, 1,0.6);
        border-radius: 50% 0;
    } */
    .header{
        display: flex;
        justify-content:flex-start;
    }
    .avatar{
        position: relative;
        width: 200px;
        height: 200px;
        margin-left: 50px;
        padding: 0;
    }
    .avatar:hover .btn-avatar{
        display: block;
    }
    .btn-avatar{
        position:absolute;
        top: 10;
        left: 0;
        display: none;
    }
    .img-avatar{
        border: 5px solid white;
        overflow: hidden;
        width: 100%;
    }
    #name{
        color: white;
        font-size: 20px;
        cursor: pointer;
        margin: 20px 0 0 20px;
    }
    .menu{
        margin: 70px 0 0 20px;
        background-color: rgba(0,0,0,0);
    }
    .item{
        color: rgb(100, 100, 100);
    }
    .item:hover{
        color: rgb(22, 135, 228);
    }
</style>


