<!--日历组件-->

<template>
    <div class='body'>
        <div v-if='type === "normal"' class='table-header' :theme='monthtype' :action='action'>
            <img src='../../../static/img/第一页.png' alt='' @click="changeDate('LastYear')">
            <img src='../../../static/img/上一页.png' alt='' @click="changeDate('LastMonth')">
            <div class='text'>{{ year }}年{{ month }}月</div>
            <img src='../../../static/img/下一页.png' alt='' @click="changeDate('NextMonth')">
            <img src='../../../static/img/最后一页.png' alt='' @click="changeDate('NextYear')">
        </div>
        <div v-else-if='type === "mini"' class='table-header-mini' :theme='monthtype'>
            <div class='text'>{{ year }}年{{ month }}月</div>
        </div>
        <table class='table' ref='table' @transitionend ='transitionend'>
            <thead>
                <tr>
                    <th>日</th>
                    <th>一</th>
                    <th>二</th>
                    <th>三</th>
                    <th>四</th>
                    <th>五</th>
                    <th>六</th>
                </tr>
            </thead>

            <tbody>
                <tr v-for="(item1,index1) in date_table" :key='index1'>
                    <td v-for="(item2,index2) in date_table[index1]" 
                    :key='index2' 
                    :class="{
                        'day' : item2,
                        'now' : item2 === today,
                        'has' : days.includes(item2),
                    }"
                    @click="selectDate(item2)"
                    >
                    {{ item2 }}
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
export default {
    props: {
        type: {
            type: String,
            default: 'normal'
        },
        date: {
            type: Date,
            default: function(){
                return new Date()
            }
        },
        days: {
            type: Array,
            default: function(){
                return []
            }
        }
    },
    data(){
        return{
            row: '',//当前日期在日历中的位置
            col: '',
            year: '',//当前日期的年份
            month: '',//当前日期的月份
            monthtype: '',//每月的类型
            today: '',//今天的日期
            now_year: '',//今天的年份
            now_month: '',//今天的月份
            date_table: [],//日历结构的二维数组
            action: '',//表格上部的动作
        }
    },
    methods: {
        //过渡结束进行处理
        transitionend(){
            this.$refs.table.removeAttribute('action');
            this.action = '';
        },
        //动态变化日历
        transCalendar(){
            //初始化日历的高度
            let height
            if(this.type === 'mini'){
                height = 180;
            }else{
                height = 36 * (this.date_table.length + 1)
            }
            this.$refs.table.style.height = height + 'px';
            //初始化日历的type
            let months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
            this.monthtype = months[this.month - 1];
        },
        //根据年、月获取日历结构
        getDateFormate(year, month){
            let d = new Date(year + "-0" + month + "-01").getDay()//当前月的1号是周几
            let days = new Date(year,month,0).getDate();//当前月的天数

            if(d === 7){//如果是星期日怎么转为数组下标第一个
                d = 0;
            }
            
            let tmp = [];
            tmp[d] = 1;
            for(let i = 2; i <= days; i++){
                if(d == 6){
                    this.date_table.push(tmp);
                    tmp = [];
                    tmp.push(i);
                    d = 0;
                }else{
                    tmp.push(i);
                    d++;
                }
            }
            this.date_table.push(tmp);
        },
        //日历初始化
        getNowFormatDate(sDate) {
            let date = new Date(sDate);
            this.year = date.getFullYear();
            this.now_year = date.getFullYear();
            this.month = date.getMonth() + 1;
            this.now_month = date.getMonth() + 1;
            this.today = date.getDate();
            //得到日历的二维数组结构
            this.getDateFormate(this.year,this.month);

            if (this.month >= 1 && this.month <= 9) {
                this.month = "0" + this.month;
            }
        },
        //改变日期
        changeDate(type){
            if(type === 'LastYear'){
                this.year = this.year - 1;
                this.month = parseInt(this.month);
                this.$refs.table.setAttribute('action','last');
            }
            else if(type === 'LastMonth'){
                this.month = parseInt(this.month) - 1;
                if(this.month === 0){
                    this.month = 12;
                    this.year = this.year - 1;
                }
                this.$refs.table.setAttribute('action','last');
            }
            else if(type === 'NextMonth'){
                this.month = parseInt(this.month) + 1;
                if(this.month === 13){
                    this.month = 1;
                    this.year = this.year + 1;
                }
                this.$refs.table.setAttribute('action','next');
            }
            else if(type === 'NextYear'){
                this.year = this.year + 1;
                this.month = parseInt(this.month);
                this.$refs.table.setAttribute('action','next');
            }

            this.action = 'act';

            this.date_table = [];
            this.getDateFormate(this.year,this.month);
            this.transCalendar()

            if(this.year === this.now_year && this.month === this.now_month){
                this.today = new Date().getDate();
            }else{
                this.today = 0;
            }

            if (this.month >= 1 && this.month <= 9) {
                this.month = "0" + this.month;
            }
        },
        selectDate(date){
            this.$emit('on-clickdate',this.year,this.month,date);
        }
    },
    created(){
        this.getNowFormatDate(this.date)
    },
    mounted(){
        this.transCalendar()
        if(this.type === 'mini'){
            this.today = 0;
            
        }
    },
    watch:{
        'date': function(newVal){
            this.date_table = []
            this.getNowFormatDate(this.date)
            let months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
            this.monthtype = months[this.month - 1];
            if(this.type === 'mini'){
                this.today = 0;
            }
        },
    }
}
</script>
<style scoped>
    .body{
        white-space: nowrap;
        text-align: center;
        cursor: pointer;
        color:#444;
        font-family: sans-serif;
    }
    .table-header{
        width: 100%;
        height: 80px;
        display:flex;
        justify-content:space-between;
        align-items: center;
        color:azure;
        font-size: 20px;
        background-color:#308ff0;
        backface-visibility: hidden;
        box-shadow: 0 6px 20px 0 rgba(0,0,0,.19),0 8px 17px 0 rgba(0,0,0,.2);
        transform-origin: bottom;
        transition: all 0.3s ease-in-out 0s;
        transform: rotateX(0deg);
        /*opacity:0.5;*//*设置半透明*/
    }
    .table-header-mini{
        width: 100%;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        color:azure;
        font-size: 15px;
        background-color:#308ff0;
        backface-visibility: hidden;
        box-shadow: 0 6px 20px 0 rgba(0,0,0,.19),0 8px 17px 0 rgba(0,0,0,.2);
    }
    .table-header[action*='act']{
        transform: rotateX(90deg);
    }
    .table-header img{
        overflow: hidden;
        width: 28px;
        height: 28px;
    }
    /*12个月份不同的样式*/
    [theme*='Jan']{ background-color: #1E88E5; }
    [theme*='Jan']+ .table th{ color: #1E88E5; }

    [theme*='Feb']{ background-color: #039BE5; }
    [theme*='Feb']+ .table th{ color: #039BE5; }

    [theme*='Mar']{ background-color: #00897B; }
    [theme*='Mar']+ .table th{ color: #00897B; }

    [theme*='Apr']{ background-color: #7CB342; }
    [theme*='Apr']+ .table th{ color: #7CB342; }

    [theme*='May']{ background-color: #FFB300; }
    [theme*='May']+ .table th{ color: #FFB300; }

    [theme*='Jun']{ background-color: #F4511E; }
    [theme*='Jun']+ .table th{ color: #F4511E; }

    [theme*='Jul']{ background-color: #8E24AA; }
    [theme*='Jul']+ .table th{ color: #8E24AA; }

    [theme*='Aug']{ background-color: #5E35B1; }
    [theme*='Aug']+ .table th{ color: #5E35B1; }

    [theme*='Sep']{ background-color: #E53935; }
    [theme*='Sep']+ .table th{ color: #E53935; }

    [theme*='Oct']{ background-color: #8E24AA; }
    [theme*='Oct']+ .table th{ color: #8E24AA; }

    [theme*='Nov']{ background-color: #6D4C41; }
    [theme*='Nov']+ .table th{ color: #6D4C41; }

    [theme*='Dec']{ background-color: #546E7A; }
    [theme*='Dec']+ .table th{ color: #546E7A; }

    .table{
        width: 100%;
        overflow: hidden;
        position: relative;
        background :cornsilk;
        box-shadow: 0 6px 20px 0 rgba(0,0,0,.19),0 8px 17px 0 rgba(0,0,0,.2);
        backface-visibility:hidden;
        transition: all 0.3s ease-in-out 0s;
    }
    .table[action=last]{
        transform: rotateY(-90deg);
    }
    .table[action=next]{
        transform: rotateY(90deg);
    }
    .table th{
        color:#308ff0;
    }
    .text, .table th,td{
        font-size: 16x;
    }
    .table .now{
        color:cornsilk;
        background: rgba(255,0,0,0.8);
        border-radius: 50%;
    }
    .table .has{
        color:cornsilk;
        background: rgba(243, 3, 151, 0.8);
        border-radius: 50%;
    }
    .table .day:hover{
        background: rgb(11, 180, 231);
        border-radius: 50%;
    }
</style>





