<!--笔记详情组件-->

<template>
    <div>
        <router-link class='return' to='/notes'><Icon size='25' type="md-arrow-round-back" /></router-link>
        <router-link class='editor' :to="{path:'/writeNote',query: {id: id}}"><Icon size='25' type="ios-brush" /></router-link>
        <Card class='main'>
            <h1 class='title'>{{ title }}</h1>
            <div class='header'>
                <Row>
                    <i-col span='6'>
                        <div class='time'>
                            {{ new Date(createdTime).getFullYear() + '年' +  (new Date(createdTime).getMonth() + 1) + '月' + new Date(createdTime).getDate() + '日'}} 
                        </div>
                    </i-col>
                    <i-col span='4'>
                        <Tag>{{ category }}</Tag>
                    </i-col>
                    <i-col span='14'>
                        <div class='page-view'>
                            阅读量:<span>{{ pageView }}</span>
                        </div>
                    </i-col>
                </Row>
            </div>
            <Divider />
            <div v-html='content' class='body'>
                {{ content }}
            </div>
        </Card>
        <Icon @click='deleteNote' class='trash' size='25' type="ios-trash" /> 
    </div>
</template>
<script>
import { reqNoteContent } from '../../api'
export default {
    data(){
        return{
            id: '',
            category: '',
            createdTime: '1997-01-01',
            pageView: 0,
            content: ''
        }
    },
    created(){
        this.getContent()
    },
    methods: {
        //删除笔记
        deleteNote(){
            this.$Modal.confirm({
                title: '确定删除笔记吗？',
                onOk: () => {
                    //this.$Message.info('确定');
                    this.$http.get('/api/deleteNote?id=' + this.id)
                    .then(res=>{
                        if(res.data.code === 1){
                            this.$Message.success('笔记删除成功')
                            this.$router.push({  
                                path:'/notes',
                            })
                        }else{
                            this.$Message.error(res.data.msg)
                        }
                    })
                    .catch(err=>{
                        console.log(err)
                    })
                },
                onCancel: () => {
                    //this.$Message.info('取消');
                }
            });
        },
        //获取笔记信息
        async getContent(){
            this.id = this.$route.query.id
            if(!this.id) return
            const result = await reqNoteContent(this.id)
            if(result.code === 1){
                this.title = result.data.title
                this.category = result.data.category
                this.createdTime = result.data.createdTime
                this.pageView = result.data.pageView
                this.content = result.data.content
                this.addPageView(this.pageView)
            }else{
                console.log(result.msg)
            }
        },
        //增加浏览量
        addPageView(pv){
            this.$http.get('/api/addPageView?id=' + this.id + '&pageView=' + (parseInt(pv) + 1))
        }
    }
}
</script>
<style scoped>
    .main{
        width: 90%;
        margin: auto;
    }
    .return{
        position: absolute;
        top: 20px;
        left: 20px;
    }
    .editor{
        position: absolute;
        top: 20px;
        right: 20px;
    }
    .body{
        text-indent: 2em;
        line-height: 2em;
        font-size: 14px;
        font-family: Arial, Helvetica, sans-serif;
        margin: 0 1em 2em 1em;
    }
    .trash{
        position: absolute;
        bottom: 20px;
        right: 20px;
    }
    .header{
        color: #333;
        font-size: 16px;
    }
    .header span{
        color: #3399ff;
    }

    /* table 样式 */
    .body >>> table {
        border-top: 1px solid #ccc;
        border-left: 1px solid #ccc;
    }
    .body >>> table td,
    .body >>> table th {
        border-bottom: 1px solid #ccc;
        border-right: 1px solid #ccc;
        padding: 3px 5px;
    }
    .body >>> table th {
        border-bottom: 2px solid #ccc;
        text-align: center;
    }

    /* blockquote 样式 */
    .body >>> blockquote {
        display: block;
        border-left: 8px solid #d0e5f2;
        padding: 5px 10px;
        margin: 10px 0;
        line-height: 1.4;
        font-size: 100%;
        background-color: #f1f1f1;
    }

    /* code 样式 */
    .body >>> code {
        display: inline-block;
        *display: inline;
        *zoom: 1;
        background-color: #f1f1f1;
        border-radius: 3px;
        padding: 3px 5px;
        margin: 0 3px;
    }
    .body >>> pre code {
        display: block;
    }

    /* ul ol 样式 */
    .body >>> ul, ol {
        margin: 10px 0 10px 20px;
    }
</style>


