            air: 0, //空气指数
            air_level: '良', //空气质量等级
            maxTem : "35℃", //最高温度
            minTem : "20℃", //最低温度
            tem  : "29℃", //平均温度
            img: '../../../static/img/wea_icon/未知.png', //天气的图片
        }
    },
    created(){
        console.log()
        if(localStorage.getItem('wea_city')){
            this.getWeather(localStorage.getItem('wea_city'));
        }else{
            this.getWeather(this.city);
            localStorage.setItem('wea_city',this.city)
        }
    },
    methods: {
        getWeather(city){
            this.$http.get('https://www.tianqiapi.com/api/?appid=54212916&appsecret=AtnpPy6Q&city=' + city)
            .then(res => {
                this.city = res.data.city;
                let d = res.data.data[0];
                this.wea = d.wea;
                this.air = d.air;
                this.air_level = d.air_level;
                this.maxTem = d.tem1;
                this.minTem = d.tem2;
                this.tem = d.tem;

                let w = this.wea.split("转");//取第一个天气
                switch(w[0]){
                    case '晴' : this.img='../../../static/img/wea_icon/晴.png'; break;
                    case '多云' : this.img='../../../static/img/wea_icon/多云.png'; break;
                    case '阴' : this.img='../../../static/img/wea_icon/云.png'; break;
                    case '小雨' : this.img='../../../static/img/wea_icon/小雨.png'; break;
                    case '阵雨' :
                    case '中雨' : this.img='../../../static/img/wea_icon/中雨.png'; break;
                    case '大雨' : this.img='../../../static/img/wea_icon/大雨.png'; break;
                    case '雷阵雨' : this.img='../../../static/img/wea_icon/雷阵雨.png'; break;
                    case '小雪' : this.img='../../../static/img/wea_icon/小雪.png'; break;
                    case '中雪' : this.img='../../../static/img/wea_icon/中雪.png'; break;
                    case '大雪' : this.img='../../../static/img/wea_icon/大雪.png'; break;
                    case '雨夹雪' : this.img='../../../static/img/wea_icon/雨夹雪.png'; break;
                    case '暴雨' : this.img='../../../static/img/wea_icon/暴雨.png'; break;
                    case '特大暴雨' : this.img='../../../static/img/wea_icon/特大暴雨.png'; break;
                    case '大暴雪' : this.img='../../../static/img/wea_icon/大暴雪.png'; break;
                    case '雨加冰雹' : this.img='../../../static/img/wea_icon/雨加冰雹.png'; break;
                    case '风' : this.img='../../../static/img/wea_icon/风.png'; break;
                    case '大风' : this.img='../../../static/img/wea_icon/风.png'; break;
                    case '龙卷风' : this.img='../../../static/img/wea_icon/龙卷风.png'; break;
                    case '飓风' : this.img='../../../static/img/wea_icon/飓风.png'; break;
                    case '浮沉' : this.img='../../../static/img/wea_icon/浮沉.png'; break;
                    case '雾霾' : this.img='../../../static/img/wea_icon/雾霾.png'; break;
                    case '雾' : this.img='../../../static/img/wea_icon/雾.png'; break;
                    case '沙尘' : this.img='../../../static/img/wea_icon/沙尘.png'; break;
                    default: this.img='../../../static/img/wea_icon/未知.png'; break;
                }
            })
            .catch(err => {
                this.$Message.error(err)
            })
        },
        selectCity(){
            this.select = true;
        },
        changeCity(){
            this.city = this.value[this.value.length - 1];
            this.select = false;
            this.$refs.wea.setAttribute('class','animated flipInX');
        },
        AnimationEnd(){
            this.$refs.wea.removeAttribute('class');
        },
        cancelCity(){
            this.select = false;
            this.edit = false;
        }
    },
    watch :{
        'city' : function(){
            this.getWeather(this.city)
            localStorage.setItem('wea_city',this.city)
        }
    }
}
</script>
<style scoped>
    .wea{
        position: relative;
        height: 100px;
    }
    .wea:hover .icon{
        display: block;
    }
    .icon{
        position:absolute;
        top: 0;
        right: 0;
        width: 15px;
        display: none;
    }
    .img{
        width: 40px;
        height: 40px;
    }
    .tem, .right{
        color: white;
        cursor:pointer;
    }
    .tem{
        font-size: 30px;
        font-weight: bold;
    }
    .right{
        margin: 0 40px 0 20px;
        font-size: 13px;
        white-space: nowrap;
        text-align: center;
    }
</style>





