<template>
    <div>
        <router-link class='return' to='/diarys'><Icon size='25' type="md-arrow-round-back" /></router-link>
        <div class='input'>
            <label>正文</label>
            <Input v-model="value" type="textarea" :autosize='{minRows: 10,maxRows: 30}' :maxlength=1000 placeholder="写入日记" />
        </div>
        <div class='btn'>
            <Button type='info' @click='Save'>保存</Button>
            <Button type='info' @click='Clear'>清空</Button>
        </div>
    </div>
</template>
<script>
import { reqDiaryContent } from '../../api'
export default {
    data(){
        return{
            value: '',
            id: 0,
        }
    },
    methods: {
        Save(){
            this.$http.post('/api/saveDiary',{
                id : this.id,
                content: this.value
            })
            .then(res=>{
                if(res.data.code === 1){
                    this.$Message.success(res.data.msg)
                }else{
                    this.$Message.error(res.data.msg)
                }
            })
            .catch(err=>{
                console.log(err)
            })
        },
        Clear(){
            this.value = '';
        },
        async getContent(){
            const result = await reqDiaryContent(this.id)
            if(result.code === 1){
                this.value = result.data.content
            }else{
                console.log(result.msg)
            }
        }
    },
    mounted(){
        this.id = this.$route.query.id;
        if(this.id){
            this.getContent()
        }
    }
}
</script>
<style scoped>
    .return{
        position: absolute;
        top: 20px;
        left: 20px;
    }
    .input{
        width: 80%;
        margin: auto;
    }
    .btn{
        margin: 10px;
        display: flex;
        justify-content:space-around;
    }
</style>




