<template>
    <div id='body'>
        <Button class='btn' icon="ios-add" type="primary" shape="circle" size="large" to="/writeNote">新笔记</Button>
        <Input size='small' v-model='key' search enter-button placeholder="请输入关键词" @on-search='Search' />
        <div class='tags'>
            <Tag 
                v-for='item in tags' 
                :key='item._id' 
                :color='item.color'
                :name='item.name'
                checkable
                closable
                fade
                @on-close="handleClose"
                @on-change='handleFilter'
            >
                {{ item.name }}
            </Tag>
            <Button icon="ios-add" type="dashed" size="small" @click="handleAdd">添加分类</Button>
        </div>
        <br />
        <div 
            class='note animated fadeInRigh' 
            v-for="item in showList" 
            :key='item._id' 
            
            @click='getContent(item._id)'
        >
            <h2 class='title'>
                {{ item.title }}
            </h2>
            <div class='content'>
                {{ item.summary }}
            </div>
            <div class='foot'>
                <Row>
                    <i-col span='6'>
                        <div class='time'>
                            <Icon type="ios-time" />
                            {{ new Date(item.createdTime).getFullYear() + '年' +  (new Date(item.createdTime).getMonth() + 1) + '月' + new Date(item.createdTime).getDate() + '日'}} 
                        </div>
                    </i-col>
                    <i-col span='4'>
                        <div class='cate'>
                            <Icon type="md-pricetag" />
                            {{ item.category }}
                        </div>
                    </i-col>
                    <i-col span='14'>
                        <div class='page-view'>
                            <Icon type="ios-eye" />
                            <span>{{ item.pageView }}</span>次浏览
                        </div>
                    </i-col>
                </Row>
            </div>
        </div>
        <Spin fix v-show="this.notes.length === 0">
            <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
            <div>加载中</div>
        </Spin>
        <!-- <div v-cloak v-show="this.filterNotes.length === 0">
            <img src='../../../static/img/什么都没有.jpg' alt="" />
        </div> -->
        <Page v-if="totalPages > 1" :page-size="pageSize" :current="1" :total="items" class='page' @on-change="changePage"/>
    </div>
</template>
<script>
//颜色标签的rgb转颜色
function rgbToColor(rgb){
    switch(rgb){
        case '#FF0000': return 'red'; break;
        case '#00FF00': return 'green'; break;
        case '#FFFF00': return 'yellow'; break;
        case '#0000FF': return 'blue'; break;
        case '#FFC0CB': return 'pink'; break;
        case '#FF00FF': return 'magenta'; break;
        case '#FF7F50': return 'volcano'; break;
        case '#FFA500': return 'orange'; break;
        case '#FFD700': return 'gold'; break;
        case '#32CD32': return 'lime'; break;
        case '#00FFFF': return 'cyan'; break;
        case '#A020F0': return 'purple'; break;
        case '#6A5ACD': return 'geekblue'; break;
        default: return rgb; break;
    }
}
//筛选的标签
//let filterTags = []
import { reqTags,reqNotes } from '../../api'
export default {
    data(){
        return{
            //搜索的关键字
            key: '',
            //标签数组
            tags: [],
            //笔记数组
            notes: [],
            //默认颜色数组
            colors:[
                'red',//red
                'green', //green
                'yellow', //yellow
                'blue', //blue
                'pink', //pink
                'magenta', //magenta,
                '#FF7F50', //volcano,
                'orange', //orange
                'gold', //gold,
                'lime', //lime,
                'cyan', //cyan
                '#6A5ACD', //geekblue,
                'purple' //purple
            ],
            color: '#BEBEBE',//选择的颜色
            filterTags: [],//筛选的数组
            filterNotes: [],//筛选后的笔记数组
            showList: [],//当前页显示的数据
            pageSize: 5,//每一页显示的笔记数
            totalItems: 0,//总笔记数
        }
    },
    computed: {
        items(){
            return this.filterNotes.length
        },
        totalPages(){
            return this.filterNotes.length / this.pageSize
        },
    },
    methods: {
        //切换页面
        changePage(index){
            let startIndex = (index - 1) * this.pageSize;
            let endIndex = index * this.pageSize;
            this.showList = this.filterNotes.slice(startIndex,endIndex)
        },
        //新增分类
        handleAdd () {
            let tmp = {
                id: 0,
                color: this.color,
                name: ''
            }
            this.$Modal.confirm({
                render: (h) => {
                    return h(
                        'div',
                        [
                            h('Input', {
                                props: {
                                    value: '',
                                    autofocus: true,
                                    placeholder: '请输入分类名称...'
                                },
                                on: {
                                    input: (val) => {
                                        tmp.name = val;
                                    },
                                },
                                style: {
                                    marginBottom: '10px'
                                }
                            }),
                            h('span','选择颜色：'),
                            h('ColorPicker', {
                                props: {
                                    value: this.color,
                                    colors: this.colors,
                                    recommend: true,
                                    editable: true,
                                },
                                on: {
                                    'on-change': (color) => {
                                        this.color = color;
                                    }
                                },
                            }),
                        ]
                    )
                },
                onOk: () => {
                    if(tmp.name){
                        tmp.color = rgbToColor(this.color);
                        this.$http.get('/api/addTag?name=' + tmp.name + '&color=' + tmp.color.replace("#","%23"))
                        .then(res=>{
                            if(res.data.code === 1){
                                tmp._id = res.data.data._id
                                this.tags.push(tmp);
                                this.$Message.success("新建分类成功")
                            }else{
                                this.$Message.error(res.data.msg)
                            }
                        })
                        .catch(err=>{
                            console.log(err)
                        })
                    }else{
                        this.$Message.error("新建分类失败，请输入相册名称")
                    }
                },
                onCancel: () => {
                    this.$Message.info('取消新建分类');
                }
            })
        },
        //删除分类
        handleClose(event, name) {
            const index = this.tags.findIndex(item=>{
                return item.name === name
            })
            const id = this.tags[index]._id
            this.$Modal.confirm({
                title: '是否删除此标签？',
                onOk: () => {
                    //this.$Message.info('确定');
                    this.$http.get('/api/deleteTag?id=' + id)
                    .then(res=>{
                        if(res.data.code === 1){
                            this.tags.splice(index, 1);
                            this.$Message.success(res.data.msg)
                        }else{
                            this.$Message.error(res.data.errmsg)
                        }
                    })
                    .catch(err=>{
                        console.log(err)
                    })
                },
                onCancel: () => {
                    //this.$Message.info('取消');
                }
            });
        },
        //筛选分类
        handleFilter(checked,name){
            if(!checked){
                this.filterTags.push(name);
            }else{
                let i = this.filterTags.indexOf(name)
                this.filterTags.splice(i,1);
            }

            //筛选后的笔记数组
            if(this.filterTags.length === 0){
                if(!this.key){
                    this.filterNotes = this.notes
                }else{
                    this.filterNotes = this.notes.filter(item=>{
                        return item.title.includes(this.key)
                    })
                }
            }else{
                this.filterNotes = this.filterNotes.filter(item=>{
                    return this.filterTags.includes(item.category)
                })
            }
            
            this.showList = this.filterNotes.slice(0,this.pageSize)
        },
        //根据关键字查找
        Search(){
            if(!this.key){
                if(this.filterTags.length === 0){
                    this.filterNotes = this.notes
                }else{
                    this.filterNotes = this.notes.filter(item=>{
                        return this.filterTags.includes(item.category)
                    })
                }
            }else{
                this.filterNotes = this.filterNotes.filter(item=>{
                    return item.title.includes(this.key)
                })
            }
            this.showList = this.filterNotes.slice(0,this.pageSize)
        },
        //获取分类标签
        async getTags(){
            const result = await reqTags()
            if(result.code === 1){
                this.tags = this.tags.concat(result.data)
            }else{
                this.$Message.error(result.msg)
            }
        },
        //获取笔记信息
        async getNotes(){
            const result = await reqNotes()
            if(result.code === 1){
                this.filterNotes = this.notes = this.notes.concat(result.data)
                this.items = this.totalItems = this.notes.length
                this.showList = this.notes.slice(0,this.pageSize)
            }else{
                this.$Message.error(result.msg)
            }
        },
        //查看笔记详情
        getContent(id){
            this.$router.push({  
                path:'/noteContent',   
                query : {
                    id: id
                }
            })
        }
    },
    created(){
        this.getTags()
        this.getNotes()
    }
}
</script>
<style>
    .ivu-color-picker-confirm-color{
        width: 150px;
    }
</style>
    
<style scoped>
    #body{
        width: 80%;
        margin: auto;
    }
    .btn{
        width: 100%;
        margin-bottom: 10px;
    }
    .note{
        margin: 0 auto 10px auto;
        padding: 15px;
        position: relative;
        background: white;
        font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
        /*border-radius: 1%;*/
        transition: all 0.3s linear;
    }
    .note:hover{
        box-shadow: 0 6px 20px 0 rgba(0,0,0,.19),0 8px 17px 0 rgba(0,0,0,.2);
    }
    .title{
        color: #464c5b;
        font-weight: bold;
        font-size: 20px;
    }
    .content{
        color: #657180;
        font-size: 16px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        box-orient: vertical;
    }
    .foot{
        color: black;
        font-size: 12px;
    }
    .foot span{
        color: #3399ff;
    }
    .page{
        text-align: center
    }
    .demo-spin-icon-load{
        animation: ani-demo-spin 1s linear infinite;
    }
    @keyframes ani-demo-spin {
        from { transform: rotate(0deg);}
        50%  { transform: rotate(180deg);}
        to   { transform: rotate(360deg);}
    }
    .demo-spin-col{
        height: 100px;
        position: relative;
        border: 1px solid #eee;
    }
</style>





