<!-- 瀑布流组件 -->

<template>
    <div id='content'>
        <!--@scrollReachBottom="getData"-->
        <vue-waterfall-easy
            ref="waterfall"
            :imgWidth='180'
            :maxCols='3'
            :imgsArr="data"
            @scrollReachBottom="getData"
            @click="clickFn"
            @imgError="imgErrorFn"
        >
            <!-- <div class="img-info" slot-scope="props">
                <p class="some-info">第{{props.index+1}}张图片</p>
                <p class="some-info">{{props.value.info}}</p>
            </div> -->
            <div slot='waterfall-head'>
                <slot name='upload'></slot>
            </div>
        </vue-waterfall-easy>
    </div>
</template>

<script>
import vueWaterfallEasy from 'vue-waterfall-easy'
export default {
    props:{
        data:{
            type: Array,
            required: true
        },
    },
    data() {
        return {
            group: 0, //记录加载数据的组数，实现懒加载
            pullDownDistance: 0,
        }
    },
    components: {
        vueWaterfallEasy
    },
    methods: {
        //点击照片事件
        clickFn(event, { index, value }) {
            event.preventDefault() //阻止冒泡事件
            if (event.target.tagName.toLowerCase() == 'img') {
                this.$emit('on-click',value._id);
                console.log('img clicked', index, value)
            }
        },
        //图片加载错误
        imgErrorFn(imgItem){
            console.log('图片加载错误',imgItem)
        },
        //获取图片数据
        getData(){
            this.group++;
            this.$emit('on-getdata',this.group);
        },
        over(){
            this.$refs.waterfall.waterfallOver()
        }
        /*pullDownMove(pullDownDistance) {
            // console.log('pullDownDistance', pullDownDistance)
            this.pullDownDistance = pullDownDistance
        },
        pullDownEnd(pullDownDistance) {
            console.log('pullDownEnd')
            if(this.pullDownDistance > 50){
                alert('下拉刷新')
            }
            this.pullDownDistance = 0
        },*/
    }
}
</script>

<style scoped>
    #content {
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100%;
    }
</style>




