<!--日记组件-->

<template>
    <div>
        <router-link class='return' to='/diarys'><Icon size='25' type="md-arrow-round-back" /></router-link>
        <router-link class='editor' :to="{path:'/writeDiary',query: {id: id}}"><Icon size='25' type="ios-brush" /></router-link>
        <Card>
            <p slot="title">{{ year }}年{{ month }}月{{ day }}日</p>
            <p slot='extra'>{{ d }}</p>
            <div class='body'>
                {{ content }}
            </div>
        </Card>
        <Icon @click='deleteDiary' class='trash' size='25' type="ios-trash" /> 
    </div>
</template>
<script>
import { reqDiaryContent } from '../../api'
export default {
    props: {
        id: {
            type: String,
            default: ''
        }
    },
    data(){
        return{
           d: 0,
           year: 1997,
           month: 1,
           day: 1,
           content: ''
        }
    },
    created(){
        this.getContent()
    },
    methods: {
        //删除日记
        deleteDiary(){
            this.$Modal.confirm({
                title: '确定删除日记吗？',
                onOk: () => {
                    //this.$Message.info('确定');
                    this.$http.get('/api/deleteDiary?id=' + this.id)
                    .then(res=>{
                        if(res.data.code === 1){
                            this.$Message.success('日记删除成功')
                            this.$router.push({  
                                path:'/diarys',
                            })
                        }else{
                            this.$Message.error(res.data.msg)
                        }
                    })
                    .catch(err=>{
                        console.log(err)
                    })
                },
                onCancel: () => {
                    //this.$Message.info('取消');
                }
            });
        },
        //获取日记内容
        async getContent(){
            const result = await reqDiaryContent(this.id)
            if(result.code === 1){
                console.log(result)
                let date = new Date(result.data.date)
                this.year = date.getFullYear()
                this.month = date.getMonth() + 1
                this.day = date.getDate()
                this.content = result.data.content
                let D = new Date(this.year + '-' + this.month + '-' + this.day);
                let days = ['星期日','星期一','星期二','星期三','星期四','星期五','星期六']
                this.d = days[D.getDay()]
            }else{
                console.log(result.msg)
            }
        }
    }
}
</script>
<style scoped>
    .return{
        position: absolute;
        top: 20px;
        left: 20px;
    }
    .editor{
        position: absolute;
        top: 20px;
        right: 20px;
    }
    .body{
        text-indent: 2em;
        line-height: 2em;
        font-size: 14px;
        font-family: Arial, Helvetica, sans-serif;
        margin: 0 1em 2em 1em;
    }
    .trash{
        position: absolute;
        bottom: 20px;
        right: 20px;
    }
</style>


