import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home/home.vue'
import Album from '../pages/Album/album.vue'
import albumInfo from '../pages/albumInfo/albumInfo.vue'
import Diarys from '../pages/Diarys/diarys.vue'
import DiaryDetails from '../pages/DiaryDetails/diaryDetails.vue'
import writeDiary from '../pages/WriteDiary/writeDiary.vue'
import Notes from '../pages/Notes/notes.vue'
import WriteNote from '../pages/WriteNote/writeNote.vue'
import NoteContent from '../pages/NoteContent/noteContent.vue'
import NotFound from '../pages/NotFound/notFound.vue'
import VueRouter from 'vue-router';

Vue.use(Router);

const Routers = ({
    mode: 'history',
    routes: [
        {
            path: '/home',
            name: 'home',
            meta: {
                title: '个人博客',
            },
            component: Home
        },
        {
            path: '/album',
            name: 'album',
            meta: {
                title: '相册',
            },
            component: Album
        },
        {
            path: '/albumInfo',
            name: 'albumInfo',
            meta: {
                title: '相册详情',
            },
            component: albumInfo
        },
        {
            path: '/diarys',
            name: 'diarys',
            meta: {
                title: '日记',
            },
            component: Diarys,
        },
        {
            path: '/diaryDetails',
            name: 'details',
            meta: {
                title: '日记详情',
            },
            component: DiaryDetails
        },
        {
            path: '/writeDiary',
            name: 'write',
            meta: {
                title: '编辑日记',
            },
            component: writeDiary
        },
        {
            path: '/notes',
            name: 'notes',
            meta: {
                title: '笔记',
            },
            component: Notes
        },
        {
            path: '/noteContent',
            name: 'content',
            meta: {
                title: '笔记详情'
            },
            component: NoteContent
        },
        {
            path: '/writeNote',
            name: 'writeNote',
            meta: {
                title: '编写笔记'
            },
            component: WriteNote
        },
        {
            path: '/',
            redirect: '/home'
        },
        {
            path: '*',
            name: 'notfound',
            meta: {
                title: '404',
            },
            component: NotFound
        }
    ]
})

export default new VueRouter(Routers);



