<template>
    <div id='main'>
        <div class='card new-card' @click='createNewAblum'>
            <img src='../../../static/img/plus.png' title='点击新建相册'/>
            <br />
            <h3>新建相册</h3>
        </div>
        <Card class='card' v-for="item in showList" :key='item._id'>
            <div style="text-align:center" @click='albumInfo(item._id)'>
                <img class='icon icon1' src='../../../static/img/删除2.png' title='删除相册' @click.stop='deleteAlbum(item._id)'/>
                <img class='icon icon2' src='../../../static/img/编辑.png' title='编辑相册' @click.stop='editAlbum(item._id)'/>
                <img :src="item.cover_img" style="width:50%">
                <br />
                <div class='title'>
                    <h3>{{ item.title }}</h3>
                </div>
            </div>
        </Card>
        <br />
        <Page v-if="totalPages > 1" :page-size="pageSize" :current="currentPage" :total="totalItems" class='page' @on-change="changePage"/>
    </div>
</template>
<script>
import { reqAlbums } from '../../api'
export default {
    data(){
        return{
            albums: [],
            showList: [],//用于展示的相册信息
            pageSize: 5,//每一页显示的相册数
            totalItems: 0,//总日相册数
            currentPage: 1,//当前页码
        }
    },
    computed: {
        totalPages(){
            return parseInt(this.totalItems / (this.pageSize + 1)) + 1
        }
    },
    methods: {
        //切换页面
        changePage(index){
            if(index === 1){
                this.showList = this.albums.slice(0,this.pageSize)
            }else{
                let startIndex = (index - 1) * this.pageSize;
                let endIndex = index * this.pageSize;
                this.showList = this.albums.slice(startIndex,endIndex)
            }
        },
        //新建相册
        createNewAblum(){
            let tmp = ''; //临时变量

            this.$Modal.confirm({
                render: (h) => {
                    return h('Input', {
                        props: {
                            value: '',
                            autofocus: true,
                            placeholder: '请输入相册名称...'
                        },
                        on: {
                            input: (val) => {
                                tmp = val;
                            },
                        }
                    })
                },
                onOk: () => {
                    if(tmp){
                        /* 数据库新增数据 */
                        this.$http.get('/api/createAlbum?title=' + tmp)
                        .then(res => {
                            if(res.data.code === 1){
                                this.$Message.success("新建相册成功")
                                this.albums.push({
                                    title: tmp,
                                    cover_img : './static/img/图片.png',
                                    _id : res.data.data._id
                                })
                                this.totalItems++;
                                this.changePage(this.totalPages)
                                this.currentPage = this.totalPages
                            }else{
                                this.$Message.error(res.data.msg)
                            }
                        })
                        .catch(err => {
                            this.$Message.error("发生错误:" + err);
                        })
                    }else{
                        this.$Message.error("新建相册失败，请输入相册名称")
                    }
                },
                onCancel: () => {
                    this.$Message.info('取消新建相册');
                }
            })
        },
        //编辑相册
        editAlbum(id){
            //根据id查找对应的相册名
            let a = this.albums.find((item)=>{
                return item._id === id;
            })

            let tmp = a.title; //临时变量

            this.$Modal.confirm({
                render: (h) => {
                    return h(
                    'div',
                    [
                        h('Input', {
                            props: {
                                value: a.title,
                                autofocus: true,
                                placeholder: '请输入相册名称...'
                            },
                            on: {
                                input: (val) => {
                                    tmp = val;
                                },
                            }
                        })
                    ])
                },
                onOk: () => {
                    /* 修改数据库的数据 */
                    this.$http.get('/api/updateAlbumTitle?id=' + id + '&title=' + tmp)
                    .then(res => {
                        if(res.data.code === 1){
                            this.$Message.success("修改相册成功")
                            a.title = res.data.title;
                        }else{
                            this.$Message.error(res.data.msg)
                        }
                    })
                    .catch(err => {
                        this.$Message.error("发生错误:" + err);
                    })
                },
                onCancel: () => {
                    this.$Message.info('取消修改相册');
                }
            })
        },
        //删除相册
        deleteAlbum(id){
            //根据id查找对应的
            let index = this.albums.findIndex((item)=>{
                return item._id === id;
            })
            
            this.$Modal.confirm({
                title: '是否删除此相册？',
                onOk: () => {
                    //this.$Message.info('确定');
                    this.$http.get('/api/deleteAlbum?id=' + id)
                    .then(res=>{
                        if(res.data.code === 1){
                            this.$Message.success('相册删除成功')
                            this.albums.splice(index,1);
                            this.totalItems--;
                            this.changePage(parseInt(index / (this.pageSize + 1)) + 1);
                            this.currentPage = parseInt(index / (this.pageSize + 1)) + 1
                        }else{
                            this.$Message.error(res.data.msg)
                        }
                    })
                    .catch(err => {
                        this.$Message.error("发生错误:" + err);
                    })
                },
                onCancel: () => {
                    //this.$Message.info('取消');
                }
            });
        },
        //跳转相册详情页
        albumInfo(id){
            this.$router.push({  
                path:'/albumInfo',
                query: {
                    id: id
                }
            })
        },
        //异步获取相册信息(id，名称，封面)
        async getAlbumsInfo(){
            const result = await reqAlbums()
            //数据读取成功
            if(result.code === 1) {
                this.albums = this.albums.concat(result.data)
                this.totalItems = this.albums.length;
                this.changePage(1)
            }else{
                this.$Message.error(result.msg)
            }
        },
    },
    created(){
        this.getAlbumsInfo()
    }
}
</script>

<style scoped>
    @keyframes rotate {
        100%{
            transform: rotate(360deg);
        }
    }
    .card{
        margin: 10px 5%;
        height: 200px;
        padding: auto;
        width: 40%;
        float: left;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .new-card{
        border: 2px dashed white;
        background: rgba(100,100,100,0.3);
        height: 200px;
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .new-card  img{
        width: 120px;
        height: 120px;
    }
    .new-card img:hover{
        animation-name: rotate;
        animation-duration: .8s;
        animation-iteration-count: 1;
        animation-timing-function: ease-in-out;
    }
    .icon{
        position: absolute;
        width: 20px;
        top: 0;
        display: none
    }
    .icon1{
        left: 0;
    }
    .icon2{
        right: 0;
    }
    .card:hover .icon{
        display: block;
    }
    .page{
        clear: both;
        text-align: center
    }
</style>





