<!--音乐播放器组件-->

<template>
    <div class='music'>
        <Tooltip content="轻触开启音乐之旅" placement="top" :delay="1000" theme="light" class='text'>
            <div @click='changeMusic'>随心而动</div>
        </Tooltip>
        <div id='music'>
            <img :src='music_info.music_img' alt='' :style='{animationPlayState: animationPlayState}' :class='animation'>
            <audio id='mp3' ref='music' @timeupdate='updateProgress' @loadedmetadata='getMusicTime' @ended='endMusic'>
                <source :src='music_info.music_src' type="audio/mpeg">
            </audio>
        </div>
        <div id='music_title'>{{ music_info.music_title }}</div>
        <div id='music_singer'>{{ music_info.music_singer }}</div>
        <div id='progress' ref='progress' @mousedown="selectTime($event)">
            <Slider v-model="music_precent" show-tip='never' id='music-progress'></Slider>
            <!-- <Progress :percent="music_precent" hide-info id='music-progress'/> -->
        </div>
        <div id='time'>
            {{ music_play_time }}
            <img :src='play_img' alt='' @click='playmusic'>
            {{ music_time }}
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            audio: null,//音乐节点
            progress: null,//进度条节点
            music_info: {
                music_img: '../../../static/img/唱片.png',
                music_src: '',
                music_title: '',
                music_singer: ''
            },
            music_precent: 0,
            music_play_time: '00:00',
            music_time: '00:00',
            music_select: false, //是否选中拖拽进度条
            play_img: '../../../static/img/播放.png',
            animation: 'play',
            animationPlayState: 'paused',
        }
    },
    props: {
        music: {
            type: Array,
            default: function(){
                return [];
            }
        }
    },
    methods: {
        //播放音乐
        playmusic() {
            if(this.audio.readyState == 0){//没有歌曲正在播放
                let l = this.music.length;
                this.changeMusic()
            }else{
                if(this.audio.paused){ //如果当前是暂停状态
                    this.audio.play(); //播放
                    if(this.audio.readyState == 4){
                        this.animationPlayState = 'running';
                        this.play_img = '../../../static/img/暂停.png'
                    }
                }else{//当前是播放状态
                    this.audio.pause(); //暂停
                    this.animationPlayState = 'paused';
                    this.play_img = '../../../static/img/播放.png'
                }
            }
        },
        //随机切换歌曲
        changeMusic(){
            let l = this.music.length;
            if(l > 0){
                let r = Math.round((Math.random() * (l - 1))); //随机选一首歌
                this.music_info.music_img = this.music[r].img;
                this.music_info.music_src = this.music[r].src;
                this.music_info.music_title = this.music[r].title;
                this.music_info.music_singer = this.music[r].singer;
                this.audio.load();
                this.audio.volume = .3;
                this.audio.play();
                if(this.animation === 'play'){
                    this.animation = 'restart';
                }else{
                    this.animation = 'play';
                }
                this.animationPlayState = 'running';
                this.play_img = '../../../static/img/暂停.png'
            }
        },
        //音乐播放结束
        endMusic(){
            this.music_precent = 0;
            this.music_time = '00:00';
            this.music_play_time = '00:00';
            this.play_img = '../../../static/img/播放.png'
            this.changeMusic();
        },
        //转换音频时长显示
        transTime(time) {
            let duration = parseInt(time);
            let minute = parseInt(duration/60);
            let sec = duration%60+'';
            let isM0 = ':';
            if(minute == 0){
                minute = '00';
            }else if(minute < 10 ){
                minute = '0'+minute;
            }
            if(sec.length == 1){
                sec = '0'+sec;
            }
            return minute + isM0+sec
        },
        //音乐加载完毕，设置音乐时间
        getMusicTime() {
            this.music_time = this.transTime(this.audio.duration)
        },
        //更新进度条
        updateProgress() {
            if(!this.music_select){
                this.music_precent = (Math.floor(this.audio.currentTime) / Math.floor(this.audio.duration)) * 100;
                this.music_play_time = this.transTime(this.audio.currentTime);
            }
        },
        //拖拽进度条
        selectTime(event) {
            if(this.music_info.music_src !== ''){//已经选择了播放歌曲
                this.music_select = true; //正在拖拽进度条
                let time;//当前进度下的时间
                document.onmousemove = e => {
                    let newWidth = e.clientX - this.$refs.progress.getBoundingClientRect().left;
                    let precent = newWidth / this.$refs.progress.offsetWidth;
                    if(precent > 1){
                        precent = 1
                    }else if(precent < 0){
                        precent = 0
                    }
                    this.music_precent = precent * 100;
                    time = this.audio.duration * precent
                    this.music_play_time = this.transTime(time);
                }
                document.onmouseup = () => {
                    document.onmousemove = document.onmouseup = null;
                    this.audio.currentTime = time;
                    this.music_select = false;
                }
                return false;
            }
        },
    },
    mounted() {
        this.audio = this.$refs.music;
        this.progress = this.$refs.progress;
    },
}
</script>
<style scoped>
    @keyframes rotate
    {
        100%{
            transform: rotate(360deg);
        }
    }
    @keyframes rotate1
    {
        100%{
            transform: rotate(360deg);
        }
    }
    .text{
        display: flex;
        justify-content: center;
        font-size: 20px;
        color:blanchedalmond;
        white-space:nowrap;
    }
    .text:hover{
        cursor: pointer;
    }
    #music{
        line-height: 0;
        overflow: hidden;
    }
    #music img{
        width: 100%;
        border-radius: 50%;
        animation-name: rotate;
        animation-duration: 20s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
    }
    #music .play{
        animation: rotate 20s infinite linear;
    }
    #music .restart{
        animation: rotate1 20s infinite linear;
    }
    #music-progress{
        padding: 0 10px;
        overflow: hidden;
    }
    #music_title{
        margin-top: 5px;
        color:black;
        font-size: 16px;
        text-align: center;
    }
    #music_singer{
        margin-top: 5px;
        text-align: center;
    }
    #time{
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>


