<template>
  <div>
      <diary :id='id' class="diary"></diary>
  </div>
</template>
<script>
    import diary from '../../components/Diary/diary.vue'
    export default {
      data(){
          return {
              id: ''
          }
      },
      components: {
          diary
      },
      //数据初始化完毕自动调用方法
      created(){
          //获取传入的参数
          this.id = this.$route.query.id
      }
  }
</script>
<style scoped>
    .diary{
        width: 80%;
        margin: auto;
    }
</style>





