<template>
    <div>
        <waterfall
            ref='waterfall'
            :data='data' 
            @on-getdata='getPhotos'
            @on-click='bigImg'
        >
            <div slot='upload'>
                <Upload
                    ref="upload"
                    :show-upload-list="false"
                    :on-success="handleSuccess"
                    :format="['jpg','jpeg','png']"
                    :max-size="2048"
                    :on-format-error="handleFormatError"
                    :on-exceeded-size="handleMaxSize"
                    :before-upload="handleBeforeUpload"
                    multiple
                    action="/api/uploadPhoto"
                    :data="uploadData"
                    
                >
                    <Button class='btn-upload' type="primary" icon="ios-camera-outline">点击上传照片</Button>
                </Upload>
            </div>
        </waterfall>
        <transition name='fade'>
            <div v-show='isBig' class='big-img'> 
                <div class='main'>
                    <div class='img' ref='img' @animationend='animationEnd' @mousewheel='changeSize'>
                        <img :width='img.width' :height='img.height' :src='img.src' @mousedown='moveImg'/>
                    </div>
                    <img class='icon' src='../../../static/img/关闭.png' title='点击关闭' @click='closeBigImg'/>
                </div>
                <div class='del'>
                    <img width='32px' src='../../../static/img/删除.png' title='点击删除照片' @click='deleteImg'/>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
import waterfall from '../../components/WaterFall/waterFall.vue'
import { reqPhotos } from '../../api'
var s = 1;//放大比例
export default {
    components: {
        waterfall,
    },
    data(){
        return{
            data: [],//相册里的照片数据
            id: '',//相册的id
            end: false,//判断相册是否已经加载完毕
            isBig: false,//是否在查看大图
            //正在查看的大图
            img: {
                src: '',
                width: 'auto',
                height: 'auto',
                index: 0,//所在数组位置的下标
            },
            group: 0,//读取数据的组数
            uploadData: {} //上传的参数
        }
    },
    methods: {
        //根据id异步获取该相册中的图片
        async getPhotos(){
            const result = await reqPhotos(this.id,this.group)
            //数据读取成功
            if(result.code === 1) {
                if(result.end){
                    this.$refs.waterfall.over()
                }else{
                    this.data = this.data.concat(result.data)
                    this.group++
                }
            }else{
                this.$Message.error(result.msg)
            }
        },
        //图片自适应
        imgAdapt(){
            let img = new Image();
            img.src = this.img.src;

            if(img.width >= img.height){
                this.img.width = '100%';
                this.img.height = 'auto';
            }else{
                this.img.height = '100%';
                this.img.width = 'auto';
            }
        },
        //查看大图
        bigImg(id){
            this.isBig = true;
            this.img.index = this.data.findIndex((item)=>{
                return item._id === id;
            })
            this.img.src = this.data[this.img.index].src;
            this.imgAdapt();
        },
        //动画结束
        animationEnd(){
            this.$refs.img.removeAttribute('move')
        },
        //滚轮改变图片大小
        changeSize(e){
            e.preventDefault() //阻止冒泡事件
            if(e.wheelDelta > 0){
                if(s < 2.0) s += 0.1 
            }else{
                if(s > 0.2) s -= 0.1
            }
            this.$refs.img.style.transform = `scale(${s})`;
        },
        //移动图片
        moveImg(event){
            event.preventDefault()//禁止图片拖动打开新标签页
            document.onmousemove = e => {
                let x = document.documentElement.clientWidth / 2 - e.clientX
                this.$refs.img.style.transform = `translateX(${-x}px)`
            }
            document.onmouseup = e => {
                if(e.clientX < document.documentElement.clientWidth / 2 - 100){
                    if(this.img.index === this.data.length - 1){
                        //最后一张则循环到第一张
                        this.img.index = 0;
                        this.img.src = this.data[this.img.index].src;
                    }else{
                        this.img.src = this.data[++this.img.index].src;
                    }
                    this.imgAdapt();
                    this.$refs.img.setAttribute('move','left');
                }else if(e.clientX > document.documentElement.clientWidth / 2 + 100){
                    if(this.img.index === 0){
                        //第一张则循环到最后一张
                        this.img.index = this.data.length - 1;
                        this.img.src = this.data[this.img.index].src;
                    }else{
                        this.img.src = this.data[--this.img.index].src;
                    }
                    this.imgAdapt();
                    this.$refs.img.setAttribute('move','right');
                }
                this.$refs.img.style.transform = 'translateX(0)' 
                document.onmousemove = document.onmouseup = null;
                s = 1;//图片比例还原
            }
            return false;
        },
        //关闭大图
        closeBigImg(){
            s = 1;//图片比例还原
            this.$refs.img.style.transform = `scale(${s})`;
            this.isBig = false;
        },
        //删除照片
        deleteImg(){
            this.$Modal.confirm({
                title: '确定删除此照片吗？',
                onOk: () => {
                    //this.$Message.info('确定');
                    if(this.data.length === 1){
                        this.$Notice.warning({
                            title: '这是最后一张照片了',
                        });
                        return;
                    }
                    this.$http.get('/api/deletePhoto?id=' + this.data[this.img.index]._id)
                    .then(res=>{
                        if(res.data.code === 1){
                            this.$Message.success('图片删除成功')
                            this.data.splice(this.img.index,1);
                            this.isBig  = false;
                            this.$http.get('/api/changeCoverImg?id=' + this.id)
                            .then(res=>{
                                if(res.data.code === 1){}
                            })
                            .catch(err=>{
                                console.log(err)
                            })
                        }else{
                            this.$Message.error(res.data.msg)
                        }
                    })
                    .catch(err => {
                        this.$Message.error("发生错误:" + err);
                    })
                },
                onCancel: () => {
                    //this.$Message.info('取消');
                }
            });
        },
        //上传成功
        handleSuccess (res, file) {
            console.log(res)
            if(res.code === 1){
                this.$Message.success('图片上传成功')
                this.data.push(res.data)
                this.$http.get('/api/changeCoverImg?id=' + this.id)
                .then(res=>{
                    if(res.data.code === 1){}
                })
                .catch(err=>{
                    console.log(err)
                })
            }else{
                this.$Message.error(res.msg)
            }
        },
        //上传的格式错误
        handleFormatError (file) {
            this.$Notice.warning({
                title: '上传文件的格式错误',
                desc: '图片格式 ' + file.name + ' 错误, 请选择.jpg，.jpeg，.png的文件'
            });
        },
        //超过可上传文件大小
        handleMaxSize (file) {
            this.$Notice.warning({
                title: '超过可上传文件大小限制',
                desc: '上传图片  ' + file.name + ' 不能超过2M'
            });
        },
        //上传处理
        handleBeforeUpload () {
            event.preventDefault() //阻止冒泡事件
            this.uploadData = {
                "id" : this.id
            }

            let promise = new Promise((resolve) => {
                this.$nextTick(function () {
                    resolve(true);
                });
            });
            return promise; //通过返回一个promise对象解决            
        }
    },
    created(){
        this.id = this.$route.query.id;
        this.getPhotos()
    },
}
</script>
<style scoped>
    @keyframes translateLeft {
        0%{
            transform: translateX(1000px)
        }
        100%{
            transform: translateX(0)
        }
    }
    @keyframes translateRight {
        0%{
            transform: translateX(-1000px)
        }
        100%{
            transform: translateX(0)
        }
    }
    .btn-upload{
        margin: 10px 25px;
        width: 540px;
    }
    .big-img{
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        width: 100vw;
        height: 100vh;
        bottom: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.8);
        z-index: 99;
        overflow: hidden;
    }
    .main{
        width: 500px;
        height: 500px;
        position: relative;
        animation: translateLeft 0.5s 1;
        /* overflow: hidden; */
    }
    [move*='left']{
        animation: translateLeft 0.5s 1;
    }
    [move*='right']{
        animation: translateRight 0.5s 1;
    }
    .img{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        cursor: pointer;
    }
    .icon{
        position: absolute;
        width: 32px;
        top: -16px;
        right: -16px;
    }
    .del{
        position: absolute;
        bottom: 32px;
    }
</style>





