<template>
    <div id='main'>
        <!-- <transition-group class="animated fadeIn" name="calendar" tag="div"> -->
            <div v-for="item in showList" :key="item.date.toString()">
                <calendar class="calendar" type="mini" :date="item.date" :days="item.days" @on-clickdate="getDate"></calendar>
            </div>
        <!-- </transition-group> -->
        <br />
        <Page v-if="totalPages > 1" :page-size="pageSize" :current="1" :total="totalItems" class='page' @on-change="changePage"/>
    </div>
</template>
<script>
import calendar from '../../components/Calendar/calendar.vue'
import { reqDiarys } from '../../api'
//取随机的提示语
const randomWord = function(words){
    let i = Math.round((Math.random() * (words.length - 1)));
    return words[i]
}
export default {
    data(){
        return{
            //存放日历的数组
            diarys: [],
            calendars: [],//所有的日历
            showList: [],//当前页显示的数据
            pageSize: 6,//每一页显示的日历数
            totalItems: 0,//总日历数
            nowWord: [
                '开始书写今天的故事吧！',
                '今天也要元气满满',
                '今日份的日记',
                '我的回合，写日记',
                'next conan 的日记',
                '人生的基本演绎法'
            ],
            pastWord: [
                '情已逝，忆未留',
                '过去的已然过去',
                '空留浮尘去',
                '曾经有份真挚的爱情摆在我面前，我却没有珍惜',
                '过去只停留在记忆之中',
                '吾断案无数，此案已忘又何妨',
                '陷阱卡发动！时光逆转',
            ],
            futureWord: [
                '坐上时光机去星际旅行',
                '世界那么大，我想去看看',
                '人生苦短，及时行乐',
                '你能看见我的未来吗',
                '洞悉千里之外，觉察十年之后',
                '穿越到未来找线索',
                '魔法卡发动！转生的希望'
            ],
        }
    },
    computed: {
        totalPages(){
            return this.totalItems / this.pageSize
        }
    },
    methods: {
        //切换页面
        changePage(index){
            let startIndex = (index - 1) * this.pageSize;
            let endIndex = index * this.pageSize;
            this.showList = this.calendars.slice(startIndex,endIndex)
        },
        //获取点击事件的所对应的日期
        getDate(year, month, date){
            let index = this.diarys.findIndex(item => {
                if(item.year === year && item.month == month && item.day == date){
                    return true
                }
            });
            if(index != -1){
                this.$router.push({  
                    path:'/diaryDetails',   
                    query : {
                        id: this.diarys[index]._id
                    }
                })
            }else{
                let d = new Date();
                if(d.getFullYear() === year && (d.getMonth() + 1) == month && date > d.getDate()){
                    this.$Message.info(randomWord(this.futureWord))
                }else if(d.getFullYear() === year && (d.getMonth() + 1) == month && date === d.getDate()){
                    this.$Message.info(randomWord(this.nowWord))
                    this.$router.push({  
                        path:'/writeDiary',   
                    })
                }else{
                    this.$Message.info(randomWord(this.pastWord))
                }
            }
        },
        async getDiaryInfo(){
            const result = await reqDiarys()
            //数据读取成功
            if(result.code === 1) {
                this.diarys = this.diarys.concat(result.data)
                let endDate = new Date();
                //得到日历数
                if(this.diarys.length === 0){
                    this.calendars.push(
                        {
                            date: endDate,
                            days: []
                        }
                    )
                    this.showList = this.calendars;
                    return;
                }
                let y = endDate.getFullYear() - this.diarys[0].year;
                let m = Math.abs(endDate.getMonth() + 1 - this.diarys[0].month);
                this.totalItems = y * 12 + m + 1;
                //将日期按年月归类
                let startYear = this.diarys[0].year;
                let startMonth = this.diarys[0].month;
                let days = [];
                for(let i = 0; i < this.totalItems; i++){
                    days[i] = [];
                }
                let i = 0;
                this.diarys.forEach(item => {
                    if(item.year === startYear && item.month === startMonth){
                        days[i].push(item.day)
                    }else{
                        i = i + (item.year - startYear) * 12 + item.month - startMonth;
                        startYear = item.year;
                        startMonth = item.month;
                        days[i].push(item.day)
                    }
                });
                //初始化日历数组
                endDate.setMonth(endDate.getMonth() + 1);
                for(let i = 0; i < this.totalItems; i++){
                    let c_date = new Date(endDate.setMonth(endDate.getMonth() - 1))
                    this.calendars.push({
                        date: c_date,
                        days: days[this.totalItems - i - 1]
                    })
                }
                //初始化显示数组
                if(this.totalItems < this.pageSize){
                    this.showList = this.calendars;
                }else{
                    this.showList = this.calendars.slice(0,this.pageSize);
                }
            }else{
                this.$Message.error(result.msg)
            }
        }
    },
    mounted(){
        //读数据库时按时间进行排序
        /*this.diarys.sort(function(a,b){
            return Date.parse(a.date) - Date.parse(b.date);
        });*/
        this.getDiaryInfo()
    },
    components: {
        calendar,
    }
}
</script>
<style scoped>
    .calendar{
        margin: 10px 5%;
        padding: auto;
        width: 40%;
        float: left;
    }
    .page{
        clear: both;
        text-align:center
    }
    .calendar-list-move {
        transition: transform 1s;
    }
</style>


