<template>
    <div id='all' :theme='theme'>
        <div id='main'>
            <div class="animated fadeIn" id='header'>
                <Header></Header>
                <weather id='wea'></weather>
            </div>
            <div id='body'>
                <Row>
                    <i-col span="6" id='left'>
                        <div class="animated flipInX">
                            <calendar class='calendar'></calendar>
                        </div>
                    </i-col>
                    <i-col span="12" id='center'>
                        <div class="animated fadeIn">
                            <!-- <transition name='router' mode='out-in'> -->
                                <router-view></router-view>
                            <!-- </transition> -->
                        </div>
                    </i-col>
                    <i-col span="6" id='right'>
                        <div class="animated fadeInRight">
                            <music class='music' :music='music'></music>
                        </div>
                    </i-col>
                </Row>
            </div>
            <Footer class="footer">&copy;Copyright 2019 conan的个人博客 | 时光</Footer>
            <BackTop></BackTop>
        </div>
    </div>
</template>

<script>
import Header from './components/Header/header.vue'
import weather from './components/Weather/weather.vue'
import calendar from './components/Calendar/calendar.vue'
import music from './components/Music/music.vue'
import { reqMusic } from './api'
const MONTHS = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec']
export default {
    data(){
        return{
            music: [],
            theme: 'Jan',
        }
    },
    components: {
        Header,
        calendar,
        music,
        weather,
    },
    methods: {
        //异步获取音乐库
        async getMusic(){
            const result = await reqMusic()
            //数据读取成功
            if(result.code === 1) {
                this.music = this.music.concat(result.data)
            }else{
                this.$Message.error(result.msg)
            }
        }
    },
    created(){
        this.theme = MONTHS[new Date().getMonth()]
        this.getMusic()
    },
    mounted(){
        /*let canvas = document.getElementById('background');
        background(canvas);*/
    }
}
</script>

<style scoped>
    *{
        margin: 0;
        padding: 0;
    }
    #main{
        width: 80%;
        margin: auto;
    }
    #header{
        padding-top: 80px;
        display: flex;
        justify-content: center;
    }
    #wea{
        margin-top: -35px;
    }
    #left{
        position: relative;
        height: 350px;
        z-index: 1
    }
    #center{
        position: relative;
        margin-top: 50px;
        margin-bottom: 50px;
        padding-top: 50px;
        padding-bottom: 50px;
        min-height: 500px;
        background:rgba(247, 244, 241, 0.5);
        z-index: 2;
    }
    #right{
        position: relative;
        height: 350px;
        z-index: 1;
    }
    .calendar{
        width: 80%;
        margin: auto;
        margin-top: 110px;
    }
    .music{
        width: 70%;
        margin-left: 40px;
        padding: 90px 0 0 0;
    }
    .footer{
        text-align: center;
        padding: 10px 0;
        background: inherit;
    }

    /*12个月份不同的主题样式*/
    [theme*='Jan']{ background:linear-gradient(rgba(186,204,217,1), rgba(186,204,217,.3)); }

    [theme*='Feb']{ background:linear-gradient(rgba(147,187,201,1), rgba(147,187,201,.3)); }

    [theme*='Mar']{ background:linear-gradient(rgba(65,179,73,1), rgba(65,179,73,.3)); }

    [theme*='Apr']{ background:linear-gradient(rgba(34,148,83,1), rgba(34,148,83,.3)); }
   
    [theme*='May']{ background:linear-gradient(rgba(69,183,135,1), rgba(69,183,135,.3)); }

    [theme*='Jun']{ background:linear-gradient(rgba(147,213,220,1), rgba(147,213,220,.3)); }

    [theme*='Jul']{ background:linear-gradient(rgba(81,196,211,1), rgba(81,196,211,.3)); }

    [theme*='Aug']{ background:linear-gradient(rgba(14,176,201,1), rgba(14,176,201,.3)); }

    [theme*='Sep']{ background:linear-gradient(rgba(249,166,51,1), rgba(249,166,51,.3)); }

    [theme*='Oct']{ background:linear-gradient(rgba(251,139,5,1), rgba(251,139,5,.3)); }

    [theme*='Nov']{ background:linear-gradient(rgba(242,123,31,1), rgba(242,123,31,.3)); }

    [theme*='Dec']{ background:linear-gradient(rgba(196,203,207,1), rgba(196,203,207,.3)); }

    .router.enter-active, .router-leave-active {
        transition: all 0.3s linear;
    }
    .router-enter, .router-leave-to{
        opacity: 0;
    }

    @media only screen and (max-width: 768px){
        
    }
</style>



